<template>
  <div class="container">
    <div class="header">
      <el-button icon="el-icon-back" circle size="mini" @click="goBack" class="back-btn"></el-button>
      <h1 class="page-title">{{ monthTitle }}{{ day }}日支出明细</h1>
    </div>

    <!-- 固定支出部分 -->
    <el-card class="expense-section">
      <div slot="header">
        <span>固定支出</span>
      </div>
      <el-table :data="fixedExpenses" style="width: 100%">
        <el-table-column prop="type" label="类别" width="180"></el-table-column>
        <el-table-column prop="amount" label="金额" width="180"></el-table-column>
        <el-table-column prop="payer" label="付款人"></el-table-column>
      </el-table>
      <div class="total">总计：{{ fixedTotal }}元</div>
    </el-card>

    <div class="summary">
      <div class="daily-limit">日常支出预算：6000</div>
      <div class="balance" :class="{ 'over-budget': balanceAmount < 0 }">
        剩余额度：{{ balanceAmount }}
      </div>
      <div class="total-expense">总支出：{{ totalAmount }}</div>
    </div>
    <!-- 日常支出部分 -->
    <el-card class="expense-section">
      <div slot="header" class="card-header">
        <span>日常支出明细</span>
        <el-button type="primary" size="small" @click="handleAdd">新增支出</el-button>
      </div>
      <el-table size="small" :data="dailyExpenses" style="width: 100%">
        <el-table-column label="明细">
          <template slot-scope="scope">
            <el-input v-if="scope.row.isEdit" v-model="scope.row.detail" size="mini"></el-input>
            <span v-else>{{ scope.row.detail }}</span>
          </template>
        </el-table-column>
        <el-table-column label="类别">
          <template slot-scope="scope">
            <el-select v-if="scope.row.isEdit" 
                      v-model="scope.row.type" 
                      size="mini" 
                      placeholder="请选择类别">
              <el-option label="城市交通" value="城市交通"></el-option>
              <el-option label="水饮咖啡" value="水饮咖啡"></el-option>
              <el-option label="零食" value="零食"></el-option>
              <el-option label="孩子玩具" value="孩子玩具"></el-option>
              <el-option label="护肤美妆" value="护肤美妆"></el-option>
            </el-select>
            <span v-else>{{ scope.row.type }}</span>
          </template>
        </el-table-column>
        <el-table-column label="金额">
          <template slot-scope="scope">
            <el-input-number v-if="scope.row.isEdit" 
                            v-model="scope.row.amount" 
                            size="mini"
                            :precision="2" 
                            :step="0.1"
                            :controls="false"
                            style="width: 100px">
            </el-input-number>
            <span v-else>{{ scope.row.amount }}</span>
          </template>
        </el-table-column>
        <el-table-column label="付款人">
          <template slot-scope="scope">
            <el-select v-if="scope.row.isEdit" 
                      v-model="scope.row.payer" 
                      size="mini"
                      placeholder="请选择付款人">
              <el-option label="张蕊" value="张蕊"></el-option>
              <el-option label="吴亮" value="吴亮"></el-option>
            </el-select>
            <span v-else>{{ scope.row.payer }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <template v-if="scope.row.isEdit">
              <el-button size="mini" type="success" @click="handleSave(scope.row)">保存</el-button>
              <el-button size="mini" @click="handleCancel(scope.row)">取消</el-button>
            </template>
            <template v-else>
              <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index)">删除</el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <div class="total">总计：{{ dailyTotal }}元</div>
    </el-card>

    <!-- 编辑/新增对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="30%">
      <el-form :model="currentItem" label-width="80px">
        <el-form-item label="明细">
          <el-input v-model="currentItem.detail"></el-input>
        </el-form-item>
        <el-form-item label="类别">
          <el-select v-model="currentItem.type" placeholder="请选择类别">
            <el-option label="城市交通" value="城市交通"></el-option>
            <el-option label="水饮咖啡" value="水饮咖啡"></el-option>
            <el-option label="零食" value="零食"></el-option>
            <el-option label="孩子玩具" value="孩子玩具"></el-option>
            <el-option label="护肤美妆" value="护肤美妆"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="金额">
          <el-input-number v-model="currentItem.amount" :precision="2" :step="0.1"></el-input-number>
        </el-form-item>
        <el-form-item label="付款人">
          <el-select v-model="currentItem.payer" placeholder="请选择付款人">
            <el-option label="张蕊" value="张蕊"></el-option>
            <el-option label="吴亮" value="吴亮"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleSave">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'DayDetail',
  data() {
    return {
      monthTitle: '',
      day: '',
      fixedExpenses: [
        { type: '住房支出', amount: 3815.41, payer: '吴亮' },
        { type: '住房支出', amount: 3078.74, payer: '张蕊' },
        { type: '住房支出', amount: 2900, payer: '张蕊' }
      ],
      dailyExpenses: [],
      dialogVisible: false,
      currentItem: {
        detail: '',
        type: '',
        amount: 0,
        payer: ''
      },
      isEdit: false,
      editIndex: -1,
      tempRow: null
    }
  },
  computed: {
    dialogTitle() {
      return this.isEdit ? '编辑支出' : '新增支出'
    },
    fixedTotal() {
      return this.fixedExpenses.reduce((sum, item) => sum + item.amount, 0).toFixed(2)
    },
    dailyTotal() {
      return this.dailyExpenses.reduce((sum, item) => sum + item.amount, 0).toFixed(2)
    },
    totalAmount() {
      return (Number(this.fixedTotal) + Number(this.dailyTotal)).toFixed(2)
    },
    balanceAmount() {
      return (6000 - Number(this.dailyTotal)).toFixed(2)
    }
  },
  created() {
    const { monthId, day } = this.$route.params
    this.monthTitle = this.getMonthTitle(monthId)
    this.day = day
    this.loadDailyExpenses()
  },
  methods: {
    getMonthTitle(monthId) {
      const months = ['一月', '二月', '三月', '四月', '五月', '六月', 
                     '七月', '八月', '九月', '十月', '十一月', '十二月']
      return months[monthId - 1]
    },
    loadDailyExpenses() {
      // 模拟数据，实际应该从API获取
      this.dailyExpenses = [
        { detail: '假期出门公交地铁', type: '城市交通', amount: 14.75, payer: '张蕊', isEdit: false },
        { detail: '买矿泉水', type: '水饮咖啡', amount: 3, payer: '张蕊', isEdit: false },
        { detail: '烤肠', type: '零食', amount: 16, payer: '吴亮', isEdit: false }
      ]
    },
    goBack() {
      this.$router.go(-1)
    },
    handleAdd() {
      this.dailyExpenses.push({
        detail: '',
        type: '',
        amount: 0,
        payer: '',
        isEdit: true,
        isNew: true
      })
    },
    handleEdit(row) {
      this.tempRow = { ...row }
      row.isEdit = true
    },
    handleSave(row) {
      if (!row.detail || !row.type || !row.amount || !row.payer) {
        this.$message.warning('请填写完整信息')
        return
      }
      row.isEdit = false
      if (row.isNew) {
        delete row.isNew
      }
      this.$message({
        type: 'success',
        message: '保存成功!'
      })
    },
    handleCancel(row) {
      if (row.isNew) {
        const index = this.dailyExpenses.indexOf(row)
        this.dailyExpenses.splice(index, 1)
      } else {
        Object.assign(row, this.tempRow)
        row.isEdit = false
      }
    },
    handleDelete(index) {
      this.$confirm('确认删除该支出记录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.dailyExpenses.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {})
    }
  }
}
</script>

<style scoped lang="scss">
.container {
  padding: 20px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 30px;
}

.back-btn {
  position: absolute;
  left: 20px;
}

.page-title {
  text-align: center;
  color: #666;
  font-size: 30px;
  margin: 0;
}

.expense-section {
  margin-bottom: 20px;
  
  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .total {
    margin-top: 15px;
    text-align: right;
    font-weight: bold;
    color: #409EFF;
  }
}

.summary {
  margin-top: 30px;
  text-align: right;
  font-size: 16px;
  
  > div {
    margin-bottom: 10px;
  }
  
  .daily-limit {
    color: #409EFF;
  }
  
  .balance {
    color: #67C23A;
    
    &.over-budget {
      color: #F56C6C;
    }
  }
  
  .total-expense {
    font-size: 18px;
    font-weight: bold;
    color: #303133;
  }
}

.el-input-number {
  width: 100px;
}

.el-select {
  width: 120px;
}
</style> 